import feathers from '@feathersjs/client';
import { library } from '@fortawesome/fontawesome-svg-core';
import { faPaperPlane } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useContext, useEffect, useRef, useState } from 'react';

import { Message } from '../../../types/Message';
import Footer from '../components/Footer';
import Header from '../components/Header';
import MessageItem from '../components/Message';
import { LoginContext } from '../context/LoginContext';
import { ThemeContext } from '../context/ThemeContext';
import useSocket from '../hooks/useSocket';

library.add(faPaperPlane);

const ENDPOINT = "/";

const Chat = () => {
  const { state } = useContext(LoginContext);
  const { theme } = useContext(ThemeContext);
  const [ messages, setMessages ] = useState<Message[]>([]);
  const [ message, setMessage ] = useState("");
  const { socket, app } = useSocket(ENDPOINT);
  const input = useRef<HTMLInputElement | null>(null);
  const container = useRef<HTMLDivElement | null>(null);

  useEffect(() => {
    app.configure(feathers.socketio(socket));
    app.service("message").on("created", async (response: Message) => {
      setMessages((prevState) => [...prevState, response]);
    });
    fetchMessages();
  }, [app, socket]);

  useEffect(() => {
    // move to bottom
    if(container.current) {
      container.current.scrollTop = container.current.scrollHeight;
    }
  }, [messages])

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setMessage(e.target.value);
  };

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    app.service("message").create({
      name: state.name,
      message,
      img: state.img,
    });
    setMessage("");
  };

  const fetchMessages = async () => {
    const allMessages: Message[] = await app.service("message").find();
    setMessages(allMessages);
  };

  return (
    <>
      <Header />
      <main className="flex flex-col flex-1 overflow-auto" ref={container}>
        <div className={`flex-1 bg-${theme}-200 p-3 pb-10`}>
          <ul className="flex flex-col px-2">
            {messages.map((message) => (
              <MessageItem context={message} key={message.id} />
            ))}
          </ul>
        </div>
      </main>
      <div>
        <form onSubmit={handleSubmit}>
          <div className="flex">
            <input
              ref={input}
              className="resize-none outline-none w-full p-2 box-border h-12"
              value={message}
              onChange={handleChange}
              placeholder="Write something..."
            />
            <button className={`outline-none bg-${theme}-800 text-white p-3 w-12 duration-300 ease-in-out transition-all hover:bg-${theme}-700`}>
              <FontAwesomeIcon icon="paper-plane" />
            </button>
          </div>
        </form>
      </div>
      <Footer />
    </>
  );
};

export default Chat;
