import React, { useContext } from 'react';

import { ThemeContext } from '../../context/ThemeContext';
import { LoginContext } from '../../context/LoginContext';

interface RadioProps extends React.HTMLProps<HTMLInputElement> {
  label: string;
}

const Radio: React.FC<RadioProps> = ({label, name, id, ...props}) => {
  const { setTheme } = useContext(ThemeContext);
  const { state, setState } = useContext(LoginContext);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    console.log(e.target.id);
    setTheme(e.target.value);
    setState({...state, img: e.target.id})
  }

  return (
    <div className="mr-3 flex items-center">
      <input type="radio" name={name} id={id} {...props} onChange={handleChange}/>
      <label htmlFor={id} className="ml-1">
        {label}
      </label>
    </div>
  )
}

export default Radio
