import React from 'react';
import {
  BrowserRouter,
  Switch,
  Route
} from 'react-router-dom';
import Chat from './pages/Chat';
import Login from './pages/Login';

const Router: React.FC = () => {
  return (
    <BrowserRouter>
      <Switch>
        <Route path="/chat">
          <Chat/>
        </Route>
        <Route path="/">
          <Login/>
        </Route>
      </Switch>
    </BrowserRouter>
  )
}

export default Router;