import React, { useContext, useState, useEffect } from "react";
import { Message } from '../../../../types/Message';
import { LoginContext } from '../../context/LoginContext';
import { ThemeContext } from '../../context/ThemeContext';
import {Transition} from 'react-transition-group';
import lucifer from '../../assets/lucifer.png';
import cheshire from '../../assets/cheshire.png';

interface MessageProps {
  context: Message;
}

const Cats = {
  lucifer,
  cheshire
}

const duration = 500;

const defaultStyle = {
  transitionDuration: `${duration}ms`,
  transitionTimingFunction: "ease-in-out",
  transitionProperty: "all",
  opacity: 0,
  transform: "scale(0)"
}

const transitionStyle = {
  entering: { opacity: 1, transform: "scale(1.05)" },
  entered:  { opacity: 1, transform: "scale(1)" },
  exiting:  { opacity: 0, transform: "scale(0)" },
  exited:  { opacity: 0, transform: "scale(0)" },
  unmounted: { opacity: 0, transform: "scale(0)" }
}

const MessageItem: React.FC<MessageProps> = ({
  context: { id, message, name, img },
}) => {
  const { state } = useContext(LoginContext);
  const { theme } = useContext(ThemeContext);
  const [ inProp, setInProp ] = useState(false);
  const self = state.name === name;

  useEffect(() => {
    setInProp(true);
  }, [])

  return (
    <Transition in={inProp} timeout={duration}>
      {changeState => (
        <li
          className={`${
            self
              ? `bg-${theme}-800 self-end self border-${theme}-800 text-${theme}-800`
              : `bg-${theme}-500 border-${theme}-500 text-${theme}-500`
          } flex flex-col p-2 my-1 max-w-md w-11/12 conversation shadow-md`}
          style={{...defaultStyle, ...transitionStyle[changeState]}}
        >
          <div className="flex text-white items-center">
            <div className="bg-white rounded-full w-10 overflow-hidden mr-2">
              <img
                src={Cats[img]}
                alt="cheshire"
                className="max-w-full"
              />
            </div>
            <p className="text-sm">
              {name}
            </p>
          </div>
          <p className="text-lg ml-10 text-white">{message}</p>
          <p className="text-xs opacity-75 text-white text-right">
            {new Date(id).toLocaleTimeString()}
          </p>
        </li>
      )}
    </Transition>
  );
};

export default MessageItem;
