import React, { useContext } from 'react';

import { LoginContext, StatesInterface } from '../../context/LoginContext';
import { ThemeContext } from '../../context/ThemeContext';

interface InputProp extends React.HTMLProps<HTMLInputElement> {
  label: string;
  entry: keyof StatesInterface;
}

const TextInput: React.FC<InputProp> = ({label, entry, type = "text"}) => {
  const {state, setState} = useContext(LoginContext);
  const { theme } = useContext(ThemeContext);
  const value = state[entry];

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const newState = {...state};
    Object.assign(newState)[entry] = e.target.value;
    setState(newState);
  }

  return (
    <div className="mt-1 mb-3 flex flex-col">
      <label className="text-xs opacity-75">{label}</label>
      <input className={`py-1 transition-all duration-300 ease-in-out bg-transparent border-b-2 outline-none focus:border-${theme}-800`} autoComplete="off" type={type} value={value} onChange={handleChange} />
    </div>
  )
}

export default TextInput
