import React, { useContext } from 'react';
import { useHistory } from 'react-router-dom';

import { TextInput, Radio } from '../components/Inputs';
import { LoginContext } from '../context/LoginContext';
import login from '../utils/login';
import Fieldset from '../components/Fieldset';
import { ThemeContext } from '../context/ThemeContext';

const Login: React.FC = () => {
  const { state } = useContext(LoginContext);
  const { theme } = useContext(ThemeContext);
  const history = useHistory();

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    if(state.name !== "" && state.roomId !== "") {
      const success = await login(state);
      
      if(success) {
        console.log({action: "Login", status: "success"});
        history.push("/chat");
      } else {
        console.log({action: "Login", status: "failed"});
      }
    }
  }

  return (
    <div className={`container mx-auto max-w-md w-full bg-${theme}-500 text-white p-3`}>
      <h1 className="text-2xl">Chat!</h1>
      <div className="mt-2">
        <form className="flex flex-col" onSubmit={handleSubmit}>
          <TextInput label="Name" entry="name" />
          <Fieldset label="Avatar">
            <Radio label="Cheshire" name="avatar" id="cheshire" value="pink" defaultChecked/>
            <Radio label="Lucifer" name="avatar" id="lucifer" value="gray"/>
          </Fieldset>
          <TextInput label="Chat Room ID" type="text" entry="roomId"/>
          <button className={`duration-300 ease-in-out transition-all mt-3 p-3 bg-${theme}-800 outline-none hover:bg-${theme}-700`}>JOIN</button>
        </form>
      </div>
    </div>
  )
}

export default Login
